.TeamPage {
display: grid;
grid-template-columns: 1fr 1fr 1fr 1fr;
grid-template-rows: 1fr 1fr 1fr;
grid-column-gap: 10px;
grid-row-gap: 10px;
}

.TeamPage .team-name-section{
    grid-column: span 3;
    justify-self: start;
    align-self: flex-end;
}
.TeamPage .win-loss-section{
    justify-self: center;
    text-align: center;
    padding: 40px;
}

.TeamPage .team-name-section .team-name{
font-size: 4rem;
margin-bottom: 20px;
}

.TeamPage .match-detail-section{
    grid-column: span 4;
}

.TeamPage .more-details{
    justify-self: center;
    align-self: center;
    font-size: 1.4rem;
}