@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a{
text-decoration: none;
color: lightgray;
}

body {
  margin: 0;
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}


.App {
  padding: 20px;
  text-align: left;
}

.MatchDetailCard {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 10px;
  margin-bottom: 20px;
  
}

.MatchDetailCard .match-date, .match-venue, .match-result {
    margin-top: 10px;
}

.MatchDetailCard .additional-detail{
    text-align: right;
}

.MatchDetailCard .additional-detail .additional-team1, .additional-team2, .additional-match-player, .additional-umpires{
    margin-bottom: 20px;
}

.MatchDetailCard.won-card{
    background-color: #4da375;
}

.MatchDetailCard.lost-card{
    background-color: #a34d5d;
}
.MatchSmallCard {
    padding: 10px;
    margin-bottom: 20px;
    
  }
  
  .MatchSmallCard.won-card{
      background-color: #4da375;
  }
  
  .MatchSmallCard.lost-card{
      background-color: #a34d5d;
  }
  .match-result{
    margin-bottom: 20px;
  }
.TeamPage {
display: grid;
grid-template-columns: 1fr 1fr 1fr 1fr;
grid-template-rows: 1fr 1fr 1fr;
grid-column-gap: 10px;
grid-row-gap: 10px;
}

.TeamPage .team-name-section{
    grid-column: span 3;
    justify-self: start;
    align-self: flex-end;
}
.TeamPage .win-loss-section{
    justify-self: center;
    text-align: center;
    padding: 40px;
}

.TeamPage .team-name-section .team-name{
font-size: 4rem;
margin-bottom: 20px;
}

.TeamPage .match-detail-section{
    grid-column: span 4;
}

.TeamPage .more-details{
    justify-self: center;
    -webkit-align-self: center;
            align-self: center;
    font-size: 1.4rem;
}
.MatchPage{
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    
}

.MatchPage .year-selector{
    justify-self: end;
    padding: 20px;
}

.MatchPage .sub-list{
    margin-bottom: 20px auto;
}
.YearSelector{
    text-align: right;
    list-style-type: none;
}

.YearSelector .list{
    margin-bottom:10px;
    margin-top: 10px
}
.TeamTile{
    text-align: center;
    height: 170px;
    border: 1px solid white;
    padding : 20px
}

.HomePage .team-grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}

.HomePage .header-section .app-name{
    text-align: center;
    font-size: 3rem;
    margin: 30px auto;
}
